.view {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100vw;
	height: 100vh;

	background-color: rgba(162, 143, 75, 0.14);
}

.progress-container {
	border: 5px solid rgba(162, 170, 75, 0.5);
	width: 400px;
	height: 30px;
}

.progress {
	height: 30px;
	background-color: rgba(162, 170, 75, 0.5);
}

.day-container {
	margin: 10px 0;
}
